import React from 'react';
import { Timeline } from 'antd';

const ProjectCard = (props) => {
    const {title,duration,description,link,image} = props.data
    return (
        <div>
      <h2>{title}</h2>
      <p>{duration}</p>
      <p>
        <Timeline>
          {
            description.map((data) => <Timeline.Item className="description">{data}</Timeline.Item>)
          }
          <Timeline.Item><a target="_blank" href={link}>See Project</a></Timeline.Item>
        </Timeline>
      </p>
   
    </div>
    )
}

export default ProjectCard